import { Component, OnInit } from '@angular/core';
import { SelectItem } from 'primeng/components/common/selectitem';
import { IDropdownSettings } from 'ng-multiselect-dropdown';

@Component({
  selector: 'app-basic-form',
  templateUrl: './basic-form.component.html',
  styleUrls: ['./basic-form.component.css']
})
export class BasicFormComponent implements OnInit {

  public formHeight:any;
  dropdownList: { item_id: number; item_text: string; }[];
  selectedItems;
  dropdownSettings:IDropdownSettings;

  constructor() { }

  ngOnInit() {
    this.dropdownList = [
      { item_id: 1, item_text: 'Chennai' },
      { item_id: 2, item_text: 'Bangaluru' },
      { item_id: 3, item_text: 'Pune' },
      { item_id: 4, item_text: 'Navsari' },
      { item_id: 5, item_text: 'New Delhi' },
      { item_id: 6, item_text: 'Mumbai' }
    ];
    this.dropdownSettings = {
      singleSelection: false,
      idField: 'item_id',
      textField: 'item_text',
      enableCheckAll: false,
      searchPlaceholderText: "Ex: Chennai",
      itemsShowLimit: 4,
      allowSearchFilter: true
    };

    let element = document.getElementsByClassName("form-content")[0];
    this.formHeight = {'width':'100%','height':element.scrollHeight +'px'};
    let mq = window.matchMedia("(max-width: 800px")
    if(mq.matches){
      this.formHeight = {'width':'100%','height':element.scrollHeight - 40 +'px'};
    }
  }

}
